// Reducer / TodoReducer


export default function reducer(state = {
    blockType: "residentBlock",
    error: null
}, action) {

    switch (action.type) {
        case "SET_BLOCK_TYPE":
            {
                return {
                    ...state,
                    blockType: action.payload
                }
            }
    }

    return state
}



