import React, {Component} from "react";
import * as PropTypes from "prop-types";

let inlinedStyles = '';
if (process.env.NODE_ENV === 'production') {
    try {
        inlinedStyles = require('!raw-loader!../public/styles.css');
    } catch (e) {
        console.log(e);
    }
}

export default class Html extends Component {
    render() {
        let css;
        if (process.env.NODE_ENV === 'production') {
            css = (<style id="gatsby-inlined-css" dangerouslySetInnerHTML={{
                __html: inlinedStyles
            }}/>);
        };

        return (
            <html lang="en">
                <head>
                    <meta charSet="utf-8"/>
                    <meta content="noindex, nofollow" name="robots" />
                    <meta name="viewport" content="width=device-width, initial-scale=1.0"/>
                    <meta name="My Residency" content="My Residency"/>
                    <title>Residency.my - offers more than just communication tools to residents and managers</title>
                    {this.props.headComponents}
                    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/3.3.7/css/bootstrap.min.css" />
                    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css" />
                    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/animate.css/2.1.1/animate.min.css" />
                    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/fullcalendar/3.4.0/fullcalendar.min.css" />

                    {/* Select2 */}
                    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/select2/4.0.3/css/select2.min.css" />

                    {/* DataTable */}
                    <link rel="stylesheet" href="https://silverfangs.github.io/datatables-custom-config/css/datatables.min.css" />

                    {/* Blueimp-gallery */}
                    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/blueimp-gallery/2.27.0/css/blueimp-gallery.min.css" />

                    {/* ClockPicker */}
                    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/clockpicker/0.0.7/jquery-clockpicker.min.css" />

                    {/* awesome-bootstrap-checkbox */}
                    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/awesome-bootstrap-checkbox/0.3.7/awesome-bootstrap-checkbox.min.css" />

                    {/* Summernote */}
                    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/summernote/0.8.2/summernote.css" />

                    {/* toastr.js */}
                    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/toastr.js/2.1.3/toastr.min.css" />

                    {/* Pace.min.js */}
                    <script type="text/javascript" src="https://cdnjs.cloudflare.com/ajax/libs/pace/1.0.2/pace.min.js"></script>
                    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/pace/1.0.2/themes/black/pace-theme-flash.min.css" />
                    {css}
                </head>
                <body>
                    <div id="___gatsby" dangerouslySetInnerHTML={{
                        __html: this.props.body
                    }}/>

                    <script type="text/javascript" src="https://cdnjs.cloudflare.com/ajax/libs/jquery/3.2.1/jquery.min.js"></script>
                    <script type="text/javascript" src="https://cdnjs.cloudflare.com/ajax/libs/jquery-easing/1.3/jquery.easing.min.js"></script>
                    <script type="text/javascript" src="https://cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/3.3.7/js/bootstrap.min.js"></script>
                    <script type="text/javascript" src="https://cdnjs.cloudflare.com/ajax/libs/moment.js/2.18.1/moment.min.js"></script>

                    <script type="text/javascript" src="https://cdnjs.cloudflare.com/ajax/libs/metisMenu/2.0.2/metisMenu.min.js"></script>
                    <script type="text/javascript" src="https://cdnjs.cloudflare.com/ajax/libs/jQuery-slimScroll/1.3.6/jquery.slimscroll.min.js"></script>


                    {/* Select2 */}
                    <script type="text/javascript" src="https://cdnjs.cloudflare.com/ajax/libs/select2/4.0.3/js/select2.min.js"></script>

                    {/* DataTable */}
                    <script type="text/javascript" src="https://silverfangs.github.io/datatables-custom-config/js/datatables.min.js"></script>

                    {/* ChartJs */}
                    <script type="text/javascript" src="https://cdnjs.cloudflare.com/ajax/libs/Chart.js/2.1.6/Chart.min.js"></script>

                    {/* Blueimp-gallery */}
                    <script type="text/javascript" src="https://cdnjs.cloudflare.com/ajax/libs/blueimp-gallery/2.27.0/js/jquery.blueimp-gallery.min.js"></script>

                    {/* Google Map */}
                    <script type="text/javascript" src="https://maps.googleapis.com/maps/api/js?key=AIzaSyCKb-szW-ZxcJgwA3N2wsbCftGj-SrDT0A"></script>

                    {/* FullCalendar */}
                    <script type="text/javascript" src="https://cdnjs.cloudflare.com/ajax/libs/fullcalendar/3.4.0/fullcalendar.min.js"></script>

                    {/* ClockPicker */}
                    <script type="text/javascript" src="https://cdnjs.cloudflare.com/ajax/libs/clockpicker/0.0.7/jquery-clockpicker.min.js"></script>

                    {/* Summernote */}
                    <script type="text/javascript" src="https://cdnjs.cloudflare.com/ajax/libs/summernote/0.8.2/summernote.min.js"></script>

                    {/* toastr.js */}
                    <script type="text/javascript" src="https://cdnjs.cloudflare.com/ajax/libs/toastr.js/2.1.3/toastr.min.js"></script>

                    {this.props.postBodyComponents}
                </body>
            </html>
        )
    }
}

Html.propTypes = {
    headComponents: PropTypes.node.isRequired,
    body: PropTypes.node.isRequired,
    postBodyComponents: PropTypes.node.isRequired
};



