// Reducer / MessageReducer

export default function reducer(state = {
    messages: [],
    currentMessage: {
        type: null,
        uuid: null,
        recipient: {
            name: null,
            type: null
        },
        chats: []
    },
    recipients: [],
    error: null
}, action) {


    switch (action.type) {
        case "MESSAGE_SET_GROUP":
            {
                return {
                    ...state,
                    messages: action.payload.messages
                }
            }

        case "MESSAGE_SELECTED":
            {
                return {
                    ...state,
                    currentMessage: action.payload.messages
                }
            }

        case 'MESSAGE_UPDATE_CHAT':
            {
                state.currentMessage.chats = state.currentMessage.chats.concat({
                    content: action.payload.content,
                    sender_uuid: action.payload.userUUID,
                    sender: action.payload.userFullname,
                    created_at: action.payload.msgCreatedAt
                });

                return {
                    ...state,
                    currentMessage: Object.assign({}, state.currentMessage)
                }
            }

        case "MESSAGE_SET_RECIPIENT":
            {
                return {
                    ...state,
                    recipients: action.payload.recipients
                }
            }

        case "MESSAGE_CLEAR":
            {
                return {
                    ...state,
                    messages: [],
                    currentMessage: {
                        type: null,
                        uuid: null,
                        recipient: {
                            name: null,
                            type: null
                        },
                        chats: []
                    },
                    recipients: []
                }
            }

        case "MESSAGE_CLEAR_CURRENT":
            {
                return {
                    ...state,
                    currentMessage: {
                        type: null,
                        uuid: null,
                        recipient: {
                            name: null,
                            type: null
                        },
                        chats: []
                    }
                }
            }
    }

    return state
}



