// Reducer / UserReducer

// Modules
import jwtDecode from 'jwt-decode';


export default function reducer(state = {
    user: {
        loggedIn: false
    },
    user_experience: {
        systemInit: false
    },
    error: null
}, action) {

    switch (action.type) {
        case "USER_SET":
            {
                let result = jwtDecode(action.payload.token);

                return {
                    ...state,
                    user: Object.assign({
                        loggedIn: true
                    }, result)
                }
            }

        case "USER_LOGOUT":
            {
                return {
                    ...state,
                    user: Object.assign({
                        loggedIn: false
                    }),
                    user_experience: Object.assign({
                        systemInit: false
                    })
                }
            }

        case "USER_EXP_SET":
            {
                return {
                    ...state,
                    user_experience: Object.assign({
                        systemInit: action.payload.systemInit
                    })
                }
            }

        case "USER_ERROR_LOGIN":
            {
                return {
                    ...state,
                    error: action.payload
                }
            }
    }

    return state
}



